import router from "@/router/router";
import { createStore } from "vuex";

const store = createStore({
    state() {
        return {
          error: "",
          user: null
        };
    },
    mutations: {

        setError(state,payload) {
            state.error = payload
        },
        UpateUser(state) {
            state.user=JSON.parse(localStorage.getItem('user'))

        }
        
    },
    actions: {
        historygoback() {
            router.go(-1)
            
        }
        
    },
    getters: {
        
    }

})


export default store