<script>
import VueApexCharts from "vue3-apexcharts";
import DashNav from "@/components/DashNav.vue";
import DashfooterPage from "@/components/DashfooterPage.vue";

export default {
 beforeRouteEnter(to, from, next) {
  next(component => {
    if (!localStorage.getItem('user')) {
      component.$router.push('/login')
    }
  })
}
,
  name:'DHomePage',
  components: {
    apexchart: VueApexCharts,
    DashNav,
    DashfooterPage,
  },
  data() {
    return {
      series: [
        {
          name: "BTC",
          type: "column",
          data: [23, 11, 22, 27, 13, 22, 37, 21, 44, 22, 30],
        },
        {
          name: "USDT",
          type: "area",
          data: [44, 55, 41, 67, 22, 43, 21, 41, 56, 27, 43],
        },
        {
          name: "ETH",
          type: "line",
          data: [30, 25, 36, 30, 45, 35, 64, 52, 59, 36, 39],
        },
      ],
      chartOptions: {
        chart: {
          height: 350,
          type: "line",
          stacked: false,
          toolbar: {
            show: false,
          },
        },
        stroke: {
          width: [0, 2, 5],
          curve: "smooth",
        },
        plotOptions: {
          bar: {
            columnWidth: "50%",
          },
        },
        fill: {
          opacity: [0.85, 0.25, 1],
          gradient: {
            inverseColors: false,
            shade: "light",
            type: "vertical",
            opacityFrom: 0.85,
            opacityTo: 0.55,
            stops: [0, 100],
          },
        },
        labels: [
          "01/01/2003",
          "02/01/2003",
          "03/01/2003",
          "04/01/2003",
          "05/01/2003",
          "06/01/2003",
          "07/01/2003",
          "08/01/2003",
          "09/01/2003",
          "10/01/2003",
          "11/01/2003",
        ],
        markers: {
          size: 0,
        },
        xaxis: {
          type: "datetime",
        },
        yaxis: {
          title: {
            // text: 'Points',
          },
        },
        tooltip: {
          shared: true,
          intersect: false,
          y: {
            formatter: function (y) {
              if (typeof y !== "undefined") {
                return y.toFixed(0) + " points";
              }
              return y;
            },
          },
        },
      },
      user: {
        firstname:'',
        ballance:'',
      },
      stringtime: new Date().getHours(),
    };
  },

  mounted() {
    this.user = JSON.parse(localStorage.getItem('user'))
    this.mygreetings()

  },
  methods: {

    mygreetings() {
       if (this.stringtime < 12) {
          this.stringtime = 'morning'
        }
        else if(this.stringtime >= 12  && this.stringtime < 18 ) {
          this.stringtime = 'afternoon'

        }
        else if(this.stringtime >= 18  && this.stringtime <= 24 ) {
          this.stringtime = 'night'

        }
      
     
    },
    togglevisibility(event) {
      let ballance = document.querySelector('.accball')
      if (event.target.innerText=='visibility') {
        ballance.innerText = '***'
        event.target.innerText='visibility_off'
       

      }
      else {
        ballance.innerText = this.user.ballance
        event.target.innerText='visibility'
      }

    }
  },
  setup() {

    
    return {
      
    }
  }
  
};
</script>

